.container {
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 800px) {
  .container {
    max-width: 800px;
  }
}

header {
  box-shadow: 0 3px 6px #00000029;
  padding: 30px 25px;
  position: static;
  top: 0;
  width: 100%;
  background: #fff;
  margin-bottom: 0;
  z-index: 0;
}
header .logo {
  display: flex;
  justify-content: center;
  align-items: center;
}
header .logo h2 {
  font: 300 32px/37px Raleway, sans-serif;
  color: #08A6E4;
  margin-left: 50px;
  cursor: pointer;
}
header nav .hamburder {
  appearance: none;
}
header nav .hamburder:checked::before {
  content: "\f00d";
  width: 58px;
  height: 38px;
  border-radius: 6px;
  color: #08A6E4;
  text-align: center;
  line-height: 38px;
  font-size: 40px;
  font-family: "Font Awesome 5 Free", sans-serif;
  font-weight: 900;
  margin-right: 5px;
}
header nav .hamburder:checked ~ ul {
  transform: translateY(0px);
}
header nav .hamburder::before {
  content: "\f0c9";
  width: 58px;
  height: 38px;
  border-radius: 6px;
  color: #08A6E4;
  text-align: center;
  line-height: 38px;
  font-size: 40px;
  font-family: "Font Awesome 5 Free", sans-serif;
  font-weight: 900;
}
header nav ul {
  position: absolute;
  top: 100px;
  left: 0;
  width: 100%;
  padding: 30px 25px;
  background-color: white;
  box-shadow: 0 3px 6px #00000029;
  transform: translateY(-210px);
  transition: transform 0.8s ease-in-out;
  z-index: -1;
}
header nav ul li {
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  line-height: 2.2rem;
}
header nav ul li a {
  color: #A5A5A5;
}
header nav ul li a:hover {
  color: #000000;
}

.banner {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.banner .container {
  margin-top: 130px;
  text-align: center;
  display: flex;
  flex-direction: column;
}
.banner .container h1 {
  text-align: center;
  font: 49px/57px Raleway;
  position: relative;
  z-index: -2;
}
.banner .container p {
  margin-top: 20px;
  margin-bottom: 38px;
}
.banner .container a {
  background: #08A6E4;
  padding: 20px;
  border-radius: 30px;
  transition: all linear 0.1s;
}
.banner .container a:hover {
  transform: scale(1.1);
}
.banner .container img {
  width: 100%;
  height: 100%;
}

main {
  width: 100%;
  background: #fff;
  position: relative;
  z-index: 1;
}
main .container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
main .container h2 {
  text-align: center;
  font: normal normal normal 36px/42px Raleway;
  padding-top: 100px;
}
main .container .option {
  width: 100%;
  margin: 100px 0 120px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
main .container .option div {
  width: 163px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  position: relative;
  margin-bottom: 30px;
}
main .container .option div h3 {
  text-align: center;
  font: normal normal 800 20px/24px Raleway;
  letter-spacing: 6px;
  color: #000;
  margin-bottom: 20px;
}
main .container .option div p {
  text-align: center;
  font-family: Raleway, sans-serif;
  font-size: 1rem;
  color: #A5A5A5;
}
main .container .option div .option_img1::after {
  content: "";
  width: 63px;
  height: 63px;
  background: #FB3B64;
  border-radius: 50%;
  margin-bottom: 13px;
}
main .container .option div .option_img2::after {
  content: "";
  width: 63px;
  height: 63px;
  background: #08A6E4;
  border-radius: 50%;
  margin-bottom: 13px;
}
main .container .option div .option_img3::after {
  content: "";
  width: 63px;
  height: 63px;
  background: #55DFB4;
  border-radius: 50%;
  margin-bottom: 13px;
}
main .container .option div img {
  position: absolute;
  top: 15px;
  right: 32px;
}

.content {
  flex-direction: column;
  background: #F7F7F7;
  padding: 70px 30px;
  position: relative;
}
.content_option {
  background: #fff;
  margin: 37px 0;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  border: 1px solid #E8E9ED;
  border-radius: 5px;
  padding: 35px;
  position: relative;
  transition: all linear 0.1s;
}
.content_option:first-of-type img {
  width: 386px;
  height: 219px;
  margin: 30px 0;
  position: relative;
  z-index: 1;
}
.content_option:nth-of-type(2) img {
  width: 386px;
  height: 288px;
  position: relative;
  margin: 30px 0;
  z-index: 1;
}

.content_position img {
  width: 296px;
  height: 208px;
  position: relative;
}

.content_position {
  transition: all linear 0.1s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.content_position .content_option {
  height: 370px;
}

.content_trumpet {
  background: #55DFB4;
  width: 100%;
  height: 370px;
  border: 1px solid #E8E9ED;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all linear 0.1s;
  margin-top: -60px;
  position: relative;
}

.content_option h2 {
  text-align: left;
  font: normal normal normal 36px/42px Raleway, sans-serif;
  margin-top: -50px;
  width: 250px;
}

.content_option h2:last-of-type {
  width: 350px;
}

.content_option p {
  text-align: left;
  font: normal normal normal 16px/22px "Open Sans", sans-serif;
  margin-top: 50px;
}

.content_substance {
  padding-top: 70px;
  height: 230px;
}

.content_substance::after {
  content: "";
  display: block;
  width: 328px;
  margin: 45px 0 20px 0;
  border-radius: 20px;
}

.container > .content_option:nth-child(1)::before {
  content: "";
  width: 150px;
  height: 150px;
  background: red;
  position: absolute;
  border-radius: 45%;
  margin-left: 240px;
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  top: 40px;
  z-index: 0;
}

.container > .content_option:nth-child(2)::before {
  content: "";
  width: 300px;
  height: 300px;
  background: #08A6E4;
  position: absolute;
  border-radius: 50%;
  top: 35px;
  margin-right: 100px;
  z-index: 0;
}

.content > .content_option:hover,
.content_option:nth-of-type(2):hover {
  cursor: pointer;
  transform: scale(1.05);
}

.content_position:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.price {
  padding: 100px 0;
  position: relative;
  z-index: 1;
  background: #fff;
  display: flex;
  flex-direction: column;
}
.price h2 {
  text-align: center;
  font: normal normal normal 36px/42px Raleway, sans-serif;
}
.price_section {
  padding: 0 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}
.price_section .price_option {
  width: 100%;
  border-radius: 18px;
  text-align: center;
  padding: 25px 20px 50px 45px;
  margin-top: 70px;
  transition: all linear 0.1s;
  position: relative;
  display: flex;
  justify-content: space-between;
}
.price_section .price_option ul li {
  padding-bottom: 20px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  line-height: 1.1rem;
}
.price_section .price_option ul li::before {
  content: url("../assets/Ckechmark-1.svg");
  width: 11px;
  height: 11px;
  padding-right: 10px;
}
.price_section .price_option .list1 li:nth-of-type(4)::before,
.price_section .price_option .list1 li:nth-of-type(5)::before {
  content: url("../assets/Close.svg");
  width: 11px;
  height: 11px;
  padding-right: 10px;
}
.price_section .price_option .list2 li:nth-of-type(5)::before {
  content: url("../assets/Close.svg");
  width: 11px;
  height: 11px;
  padding-right: 10px;
}
.price_section .price_option::after {
  content: "";
  width: 150px;
  height: 50px;
  border-radius: 23px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  font: normal normal bold 13px/41px "Open Sans", sans-serif;
  color: #fff;
  bottom: -25px;
  left: 50%;
  transform: translateX(-50%);
}
.price_section .price_option:first-of-type {
  border: 1px solid #FB3B64;
}
.price_section .price_option:nth-of-type(2) {
  border: 1px solid #08A6E4;
}
.price_section .price_option:nth-of-type(3) {
  border: 1px solid #55DFB4;
}
.price_section .price_option:first-of-type::after {
  content: "Begin";
  background: #FB3B64;
}
.price_section .price_option:nth-of-type(2)::after {
  content: "Begin";
  background: #08A6E4;
}
.price_section .price_option:nth-of-type(3)::after {
  content: "Begin";
  background: #55DFB4;
}
.price_section .price_option:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.price_section .price_option:nth-of-type(1):hover {
  box-shadow: 5px 10px 27px rgba(248, 81, 118, 0.23);
}
.price_section .price_option:nth-of-type(2):hover {
  box-shadow: 5px 10px 27px rgba(7, 172, 230, 0.19);
}
.price_section .price_option:nth-of-type(3):hover {
  box-shadow: 5px 10px 27px rgba(58, 253, 197, 0.24);
}
.price_section .price_name {
  position: relative;
  top: 50px;
}
.price_section .price_name h3 {
  font: normal normal bold 20px/1px Raleway, sans-serif;
  color: #13253B;
  padding-bottom: 15px;
}
.price_section .price_name .p1 {
  font: normal normal 300 46px/2px "Open Sans", sans-serif;
  color: #FB3B64;
  padding: 35px 0 100px 0;
}
.price_section .price_name .p2 {
  font: normal normal 300 46px/2px "Open Sans", sans-serif;
  color: #08A6E4;
  padding: 35px 0 40px 0;
}
.price_section .price_name .p3 {
  font: normal normal 300 46px/2px "Open Sans", sans-serif;
  color: #55DFB4;
  padding: 35px 0 40px 0;
}
.price_section .price_name h5 {
  font: normal normal bold 12px/1px Raleway, sans-serif;
  color: #13253B;
  padding: 0 0 62px 0;
}
.price .price_option:first-of-type > .price_name {
  margin-left: 34px;
}
.price .price_option:last-of-type > .price_name {
  margin-left: 15px;
}

.calc {
  padding: 80px 0;
  background-color: #F7F7F7;
}
@media (max-width: 1024px) {
  .calc {
    padding: 30px 15px;
  }
}
.calc h2 {
  text-align: center;
  font: normal normal normal 36px/42px Raleway, sans-serif;
}
.calc__columns {
  max-width: 865px;
  margin: 50px auto 0;
  display: flex;
}
@media (max-width: 1024px) {
  .calc__columns {
    display: block;
  }
}
.calc__form {
  width: 50%;
  margin-right: 20px;
}
@media (max-width: 1024px) {
  .calc__form {
    width: 100%;
    margin-right: 0;
  }
}
.calc__input input, .calc__select .select__input {
  color: #A5A5A5;
  width: 100%;
  border-radius: 6px;
  border: 1px solid #08A6E4;
  padding: 11px 20px;
  line-height: 1em;
  font-size: 0.8125rem;
  box-shadow: 0px 3px 6px rgba(7, 172, 230, 0.19);
  margin-bottom: 20px;
  background-color: #fff;
}
.calc__input input::placeholder {
  color: #A5A5A5;
}
.calc__select {
  position: relative;
}
.calc__select .select__input {
  position: relative;
  height: 40px;
  vertical-align: middle;
  padding: 13px 20px;
  cursor: pointer;
}
.calc__select .select__input::after {
  content: "";
  background-image: url("../../assets/Arrow.svg");
  width: 22px;
  height: 22px;
  display: block;
  position: absolute;
  right: 14px;
  top: 9px;
}
.calc__select .select__dropdown {
  display: none;
  position: absolute;
  z-index: 1;
  background-color: white;
  width: 100%;
  border-radius: 6px;
  border: 1px solid #FB3B64;
  overflow: hidden;
}
.calc__select .select__dropdown li {
  font-size: 13px;
  padding: 10px 20px;
  cursor: pointer;
}
.calc__select .select__dropdown li:hover {
  background-color: #F7F7F7;
}
.calc__select.open .select__input::after {
  transform: rotate(180deg);
}
.calc__select.open .select__dropdown {
  display: block;
}
.calc .form__checkbox {
  display: flex;
  margin-bottom: 20px;
}
.calc .form__checkbox input[type=checkbox] {
  appearance: none;
  width: 24px !important;
  height: 24px;
  border: 1px solid #95989A;
  border-radius: 4px;
  margin-right: 5px;
  padding: 3px;
  cursor: pointer;
}
.calc .form__checkbox input[type=checkbox]:checked {
  content: url("../assets/Checkmark.svg");
  display: flex;
  justify-content: center;
  align-items: center;
}
.calc .form__checkbox .consent__text {
  padding-top: 4px;
  text-align: left;
  font: normal normal normal 12px/17px "Open Sans", sans-serif;
  color: #A5A5A5;
}
.calc__summary {
  width: 50%;
  margin-left: 20px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1024px) {
  .calc__summary {
    width: 100%;
    margin-left: 0;
    margin-top: 30px;
  }
}
.calc__summary .summary__total,
.calc__summary .list__item {
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  border-radius: 6px;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-size: 0.875rem;
  display: none;
}
.calc__summary .summary__total.open,
.calc__summary .list__item.open {
  display: flex;
}
.calc__summary .summary__total {
  width: 50%;
  align-self: flex-end;
  margin-top: 22px;
  padding: 8px 20px;
  background-color: #08A6E4;
}
.calc__summary .summary__total .total__price {
  font-size: 1.25rem;
  font-weight: 700;
}
.calc__summary .list__item {
  padding: 6px 20px;
  background-color: #55DFB4;
  margin-bottom: 8px;
}
.calc__summary .list__item .item__price {
  font-weight: 700;
  font-size: 1.125rem;
}

.contact {
  padding: 50px 30px;
  background: #fff;
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact .contact_question .contact_que_info {
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
}

.contact_question h2 {
  font: normal normal normal 49px/57px Raleway, sans-serif;
  width: 350px;
  padding-bottom: 22px;
}

.contact_question p {
  text-align: left;
  font: normal normal normal 16px/22px "Open Sans", sans-serif;
  padding-bottom: 42px;
}

.contact_que_info p {
  text-align: left;
  font: normal normal 600 16px/22px "Open Sans", sans-serif;
  color: #A5A5A5;
  padding-bottom: 0;
  display: inline-block;
  margin-right: 50px;
  cursor: pointer;
}

.contact_que_info p:nth-of-type(2) {
  padding: 0;
  margin: 0;
}

.contact_area {
  display: flex;
  flex-direction: column;
  padding: 0 15%;
}

.contact_area > label:first-of-type,
.contact_area label:nth-of-type(2) {
  text-align: left;
  font: normal normal bold 11px/15px Open Sans;
  letter-spacing: 3px;
  padding-bottom: 2px;
  color: #08A6E4;
}

.checkbox_area {
  display: flex;
}

.checkbox_area label {
  text-align: left;
  font: normal normal normal 12px/17px "Open Sans", sans-serif;
  color: #A5A5A5;
}

input[type=text],
input[type=email] {
  appearance: none;
  outline: none;
  border: none;
  border-bottom: 1px solid #A5A5A5;
  border-right-width: 460px;
  margin-bottom: 30px;
  font-size: 25px;
}

.checkbox_area input[type=checkbox]:checked {
  content: url("../assets/Checkmark.svg");
  display: flex;
  justify-content: center;
  align-items: center;
}

.checkbox_area input[type=checkbox] {
  appearance: none;
  width: 24px !important;
  height: 24px;
  border: 1px solid #95989A;
  border-radius: 4px;
  margin-right: 5px;
  padding: 3px;
  cursor: pointer;
}

.contact button {
  width: 200px;
  height: 50px;
  background: #08A6E4;
  box-shadow: 5px 10px 14px rgba(7, 172, 230, 0.19);
  border-radius: 35px;
  border: none;
  margin-top: 20px;
  align-self: flex-end;
  color: #fff;
  font: normal normal bold 13px/18px "Open Sans", sans-serif;
  cursor: pointer;
  transition: all linear 0.1s;
}

.contact button:hover {
  transform: scale(1.1);
}

.contact_que_info p:first-of-type::before {
  content: url("../assets/Mail Icon.svg");
  position: relative;
  left: 0;
  bottom: -8px;
}

.contact_que_info p:nth-of-type(2)::before {
  content: url("../assets/Phone Icon.svg");
  position: relative;
  left: 0;
  bottom: -8px;
}

footer {
  width: 100%;
  height: 89px;
  background: #F7F7F7;
  box-shadow: 0 -5px 40px #00000029;
  padding: 20px 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer h2 {
  font: normal normal 300 26px/30px Raleway, sans-serif;
  color: #08A6E4;
}

footer h2:hover {
  cursor: pointer;
}

footer h6 {
  font: normal normal normal 11px/15px "Open Sans", sans-serif;
  color: #A5A5A5;
}

.footer_icons img:last-of-type {
  padding: 20px;
}

.footer_icons img:hover {
  cursor: pointer;
}